div.tfw-layout-stack,
div.tfw-layout-stack > div {
  position: absolute;
  left    : 0;
  top     : 0;
  width   : 100%;
  height  : 100%;
  margin  : 0;
  padding : 0;
}
@-webkit-keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-icon {
    display: inline-block;
    -webkit-transition: all .3s;
    transition: all .3s;
    margin: 2px;
    padding: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.tfw-view-icon.zero {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
}

.tfw-view-icon.active {
    cursor: pointer;
}

.tfw-view-icon.animate {
    animation: tfw-view-icon-rotation 1.1s linear infinite;
    -webkit-animation: tfw-view-icon-rotation 1.1s linear infinite;
}
button.tfw-view-button {
  color          : inherit;
  display        : inline-flex;
  flex-direction : row;
  justify-content: center;
  align-items    : center;
  margin         : 4px;
  height         : 2rem;
  border         : none;
  -webkit-transition     : all .2s;
  transition     : all .2s;
  vertical-align : middle;
  position       : relative;
  min-height     : 36px;
  padding        : 0;
  text-decoration: none;
  border-radius  : 2px;
  text-shadow    : none;
  cursor         : pointer;
  font-family    : Roboto, josefin-sans, sans-serif;
  font-size      : 14px;
  font-weight    : bold;
}
button.tfw-view-button.floating {
  border-radius: 100%;
  width        : 56px;
  height       : 56px;
}
button.tfw-view-button.floating.small {
  width     : 40px;
  min-width : 40px;
  height    : 40px;
  max-height: 40px;
}
button.tfw-view-button.wide {
  width  : calc(100% - 8px);
  display: flex;
}
button.tfw-view-button.hide,
button.tfw-view-button.hide.wide {
  display: none;
}
button.tfw-view-button.flat {
  background-color: transparent;
}
button.tfw-view-button[!disabled]:hover {
  -webkit-filter: saturate(100%) brightness(120%);
          filter: saturate(100%) brightness(120%);
}
button.tfw-view-button > div.text.hide {
  display: none;
}
button.tfw-view-button > div.text {
  flex-grow     : 0;
  order         : 1;
  text-overflow : ellipsis;
  white-space   : nowrap;
  overflow      : hidden;
  text-align    : left;
  text-transform: uppercase;
  margin        : 0 8px;
}
button.tfw-view-button.inverted > div.text {
  order     : -1;
  text-align: right;
}
button.tfw-view-button.no-text > div.text {
  display: none;
}
button.tfw-view-button > .icon {
  order    : 0;
  flex-grow: 0;
  margin   : 0 8px 0 4px;
  padding  : 0;
}
button.tfw-view-button[disabled],
button.tfw-view-button[disabled]:hover {
  pointer-events : none;
  text-decoration: none;
  cursor         : default;
  -webkit-filter: saturate(0);
          filter: saturate(0);
  text-shadow    : none;
  box-shadow     : none;
}
button.tfw-view-button[disabled] > *,
button.tfw-view-button[disabled]:hover > * {
  opacity: .5;
}
button.tfw-view-button:focus {
  text-decoration      : underline;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
}
/* On small screen, responsive mode hides the text when there is an icon. */
@media (max-width: 480px) {
  button.tfw-view-button.responsive > div.text {
    display: none;
  }
  button.tfw-view-button.responsive {
    max-width    : 36px;
    min-width    : 36px;
    width        : 36px;
    width        : 36px;
    margin       : 0;
    border-radius: 100%;
    overflow     : hidden;
  }
}
/* Small version */
button.tfw-view-button.small {
  min-height : 28px;
  max-height : 28px;
  height     : 28px;
  margin     : 2px 4px;
  font-size  : 10px;
  font-weight: normal;
}
button.tfw-view-button.small > div.text {
  text-align: left;
}
button.tfw-view-button.smal > .tfw-view-icon {
  margin: 0 4px 0 2px;
}
