div.tfw-behavior-touchable {
  position      : absolute;
  overflow      : hidden;
  z-index       : 999999;
  pointer-events: none;
  background    : transparent;
}
div.tfw-behavior-touchable > div {
  position        : absolute;
  border-radius   : 100%;
  opacity         : .7;
  -webkit-transform       : scale(0);
          transform       : scale(0);
  background-color: #000;
}
div.tfw-behavior-touchable > div.open {
  opacity                   : .1;
  -webkit-transform                 : scale(1);
          transform                 : scale(1);
  -webkit-transition                : all .5s ease;
  transition                : all .5s ease;
  -webkit-transition-timing-function: cubic-bezier(0.7, 1, .5, -.5);
          transition-timing-function: cubic-bezier(0.7, 1, .5, -.5);
}
div.tfw-behavior-touchable > div.show {
  -webkit-transform : scale(1);
          transform : scale(1);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}
div.tfw-view-scroller {
  overflow: auto;
  position: relative;
}
div.tfw-view-scroller > div.body {
  position  : absolute;
  left      : 0;
  top       : 0;
  width     : 100%;
  height    : 100%;
  margin    : 0;
  -webkit-filter : grayscale(0);
          filter : grayscale(0);
  -webkit-transition: -webkit-filter .5s;
  transition: -webkit-filter .5s;
  transition: filter .5s;
  transition: filter .5s, -webkit-filter .5s;
}
div.tfw-view-scroller.refreshing > div.body {
  -webkit-filter : grayscale(1);
          filter : grayscale(1);
  -webkit-transition    : none;
  transition    : none;
  pointer-events: none;
}
div.tfw-view-scroller > div.screen {
  position      : absolute;
  left          : 0;
  top           : 0;
  right         : 0;
  bottom        : 0;
  pointer-events: none;
  z-index       : 1;
  text-align    : center;
  overflow      : hidden;
}
div.tfw-view-scroller > div.screen > button {
  -webkit-transform : translateY(-110%);
          transform : translateY(-110%);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
div.tfw-view-scroller.animate-refresh > div.screen > button {
  -webkit-transform : translateY(100%);
          transform : translateY(100%);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}
div.guide-view-card {
  position     : relative;
  margin       : 8px 4px;
  border-radius: 4px;
  overflow     : hidden;
}
div.guide-view-card > div {
  margin: 0;
}
div.tfw-view-touchable.enabled {
  cursor: pointer;
}
div.tfw-view-image {
  content            : ".";
  font-size          : 0;
  opacity            : 0;
  background-position: left top;
  background-size    : cover;
  background-repeat  : no-repeat;
}
div.tfw-view-image.show {
  opacity   : 1;
  -webkit-transition: opacity .7s;
  transition: opacity .7s;
}
div.guide-view-pack {
  position           : relative;
  height             : calc(3rem + (100vw - 8px) / 2);
  background-position: left top;
  background-size    : contain;
  background-repeat  : no-repeat;
  overflow           : hidden;
}
div.guide-view-pack > div.title {
  position   : absolute;
  left       : 0;
  top        : 0;
  width      : 100%;
  height     : 3rem;
  text-align : center;
  font-weight: bold;
  padding    : 4px;
  margin     : 0;
}
div.guide-view-pack > div.desc {
  position     : absolute;
  left         : 0;
  bottom       : 0;
  padding      : 4px;
  margin       : 0;
  height       : 3rem;
  overflow     : hidden;
  text-overflow: ellipsis;
}
div.guide-view-pack > div {
  z-index: 1;
}
div.guide-view-pack > div.background {
  z-index : 0;
  position: absolute;
  left    : 0;
  top     : 0;
  width   : 100%;
  height  : calc(100% - 3rem);
}
