div.guide-view-pack {
  position           : relative;
  height             : calc(3rem + (100vw - 8px) / 2);
  background-position: left top;
  background-size    : contain;
  background-repeat  : no-repeat;
  overflow           : hidden;
}
div.guide-view-pack > div.title {
  position   : absolute;
  left       : 0;
  top        : 0;
  width      : 100%;
  height     : 3rem;
  text-align : center;
  font-weight: bold;
  padding    : 4px;
  margin     : 0;
}
div.guide-view-pack > div.desc {
  position     : absolute;
  left         : 0;
  bottom       : 0;
  padding      : 4px;
  margin       : 0;
  height       : 3rem;
  overflow     : hidden;
  text-overflow: ellipsis;
}
div.guide-view-pack > div {
  z-index: 1;
}
div.guide-view-pack > div.background {
  z-index : 0;
  position: absolute;
  left    : 0;
  top     : 0;
  width   : 100%;
  height  : calc(100% - 3rem);
}